.product-preview,
.skillsets,
.about-banner,
.contact-section,
.journal,
.information-counter {
    font-family: $font__m;
    text-align: center;
    .title-header {
        @include titles;
    }
}

.home {
    .breadcrumbs {
        display: none;
    }
    #content {
        @include desktop {
            margin-top: -19px;
        }
    }
}

.fullscreen-text {
    font-family: $font__m;
    color: white;
    text-align: center;
    padding: 60px 20px 0 20px;
    @include tablet {
        padding-top: 114px;
    }
    @include desktop{
        padding-top: 140px;
        text-align:left;
        max-width: 1200px;
        margin: auto;
    }
    .title {
        font-size: 28px;
        font-weight: 400;
        margin: 0;
        text-transform: uppercase;
        @include tablet {
            font-size: 32px;
        }
    }
    .subtitle {
        font-size: 25px;
        font-weight: 200;
        margin: 0;
        line-height: 1.12;
        @include tablet {
            font-size: 30px;
            line-height: 1.14;
        }
    }
    .text-button {
        @include button;
    }
}

.headerimage {
    background-image: url("../../resources/graphics/backgrounds.jpg");
    box-shadow: 0px 516px rgba(61, 96, 151, 0.6) inset;
    height: 413px;
    width: 100%;
    background-size: cover;
    position: relative;
    z-index: -1;
    background-position-y: -6px;
    @include tablet {
        height: 488px;
            background-position-y: 0px;
    }
    @include desktop{
        height: 564px;
    background-position-y: 17px;
    box-shadow: 0px 562px rgba(61, 96, 151, 0.6) inset;
    }
}

#backshape .wrapper {
    display: block;
    float: left;
    margin: 24px auto 41px;
    width: 100%;
    padding: 0 5%;
    transform: skew(0deg, 8deg);
    -ms-transform: skew(0deg, -10deg);
    -webkit-transform: skew(0deg, -5deg);
    @include tablet{
        -webkit-transform: skew(0deg, 8deg);
    }
    @include desktop{
        margin:0;
            -webkit-transform: skew(0deg, 8deg);
    }
}

.product {
    float: left;
    min-height: 200px;
    background: transparent;
    transform: skew(0deg, 8deg);
    -ms-transform: skew(0deg, -10deg);
    /* IE 9 */
    -webkit-transform: skew(0deg, 0deg);
    /* Safari and Chrome */
}

#backshape,
#backshape-about,
#backshape-counter,
#backshape-contact {
    z-index: 1;
    display: block;
    float: left;
    margin-top: -80px;
    width: 100%;
    background: white;
    transform: skew(0deg, 10deg);
    -ms-transform: skew(0deg, 10deg);
    /* IE 9 */
    -webkit-transform: skew(0deg, -8deg);
}

//product-preview
.product-product{
    @include desktop{
        float:none;
        display:flex;
        justify-content:space-between;
    }
}
.product-preview {
    @include tablet {
        padding: 0;
    }
    .title-header {
        max-width: 850px;
        margin: auto;
        @include alt-header;
        @include desktop{
            text-align:right;
        }
    }
    .products-selection{
        @include tablet{
            width: 100%;
        }
    }
    .product-tag {
        border: 1px solid black;
        padding: 10px;
        text-transform: uppercase;
        font-weight: 100;
        margin: 0;
        display: inline-block;
        width: 47%;
        margin-bottom: 10px;
        margin-right: 4px;
        font-size: 14px;
        @include tablet {
            width: 25%;
        }
        @include desktop {
            width: auto;
        }
    }
    .all {
        width: 100%;
        @include tablet {
            width: 25%;
        }
        @include desktop {
            width: auto;
        }
    }
    .products-grid {
        display: none;
        @include desktop {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            img {
                width: auto;
                height: 153px;
            }
        }
    }
}

.paper {
    background-image: url("../../resources/graphics/paper.png");
}

//about
#backshape-about {
    background-color: $color__blue;
    margin-top: 0px;
    @include desktop{
    transform: skew(0deg, 0deg);
    }
}

.about-banner {
    padding: 23px 20px 15px;
    transform: skew(0deg, 8deg);
    color: white;
    font-family: $font__m;
    @include tablet {
        margin: 20px auto 0;
    }
    @include desktop{
        transform: skew(0deg, 0deg);
    }
    .title-header {
        @include alt-header;
        .description {
            font-weight: 300;
            &:after {
                background-color: white;
            }
        }
    }
    .readmore-button {
        @include button;
    }
    p {
        font-weight: 100;
        font-size: 17px;
        @include tablet {
            font-size: 20px;
        }
    }
    .about-readmore {
        display: none;
    }
    .about-readmore.show {
        display: block;
    }
}

.about-banner.shrink {
    padding: 23px 20px 24px;
}

//skills
#backshape-counter {
    margin-top: 0px;
}

.information-counter {
    padding: 0 20px;
    transform: skew(0deg, 8deg);
    margin-top: 27px;
    margin-bottom: 11px;
    background-color: white;
    color: black;
    max-width: 1200px;
    @media (min-width: 500px) {
        margin-top:35px;
        margin-left: 12%;
        margin-right: 12%;
    }
    @include tablet{
        margin-bottom: 55px;
        margin-top:55px;
    }
    @include desktop {
        position: relative;
    }
    .title-header {
        @include alt-header;
        width: 100%;
        max-width: 800px;
        margin: auto;
    }
    .information-container {
        @include tablet {
            display: flex;
            justify-content: space-between;
        }
        .counter {
            @include tablet {
                width: 31.3333%;
            }
            @include desktop {
                width: auto;
            }
            img {
                height: 52px;
                max-width: 61%;
            }
            .counter-text {
                display: inline-block;
                text-align: left;
                margin-left: 10px;
                @include tablet {
                    text-align: center;
                }
                @include desktop {
                    text-align: left;
                }
                .counter-value {
                    font-size: 25px;
                    font-weight: 400;
                    margin-bottom: 0;
                    @include desktop {
                        font-size: 38px;
                    }
                }
                .count-description {
                    font-weight: 100;
                    font-size: 20px;
                    margin-top: 0;
                    @include desktop {
                        font-size: 20px;
                    }
                }
            }
        }
    }
    &:after {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        border-left: 35px solid transparent;
        border-right: 35px solid transparent;
        border-top: 25px solid white;
        left: calc(52% - 40px);
        z-index: 20;
        margin-top: 18px;
    }
}

//counter
#backshape-contact {
    margin-top: 0px;
    background-image: url("../../resources/graphics/contact-bg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include desktop{
        background-attachment: fixed;
    }
}

.contact-section {
    margin-top: 17px;
    padding: 0 20px;
    transform: skew(0deg, 8deg);
    color: white;
    margin-bottom: 58px;
    @include desktop {
        padding: 0px 20px 50px;
    }
    .contact-large-container {
        @include desktop {
            @include desktopalign;
            display: flex;
            justify-content: space-between;
        }
    }
    .address,
    .phone,
    .email {
        font-weight: 200;
    }
    section {
        display: none;
        @include desktop {
            display: block;
            width: 48%;
        }
    }
    .contact-container {
        @include tablet {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            font-size: 18px;
            font-weight: 100;
        }
        @include desktop {
            width: 48%;
        }
        .title-header,
        .container-address {
            font-weight: 200;
            @include tablet {
                width: 100%;
            }
        }
        .title-header {
            margin: 20px 0;
        }
        .container-phone,
        .container-email {
            font-weight: 200;
            @include tablet {
                width: 47%;
                margin-top: 0;
            }
        }
        .container-phone {
            @include tablet {
                text-align: right;
            }
        }
        .container-email {
            @include tablet {
                text-align: left;
            }
        }
    }
}